@import url('https://fonts.googleapis.com/css?family=Lato:300,400,400i,700&display=swap');
@import "vars";
@import "base";
@import "header";
@import "footer";
@import "media";

#menu__toggle {
    display: none;
}

body {
    font-family: 'Lato', sans-serif;
}

.wrapper {
    overflow: hidden;
    min-height: 100%;
}

.page {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
}

.main-screen {
    flex: 1 0 100%;
    position: relative;

    /* .main-screen__bg */
    &__bg {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
}

.generation {
    padding: 120px 0;
    background-color: #f7f7f7;
    /* .genration__block */

    @media screen and (max-width: 900px) {
        padding: 80px 0;
    }
    @media screen and (max-width: 600px) {
        padding: 30px 0;
    }
}
.block {
    text-align: center;
    /* .block__icon */

    &__icon {
        margin: 0 0 33px 0;
    }

    /* .block__title */

    &__title {
        color: #353738;
        font-size: 18px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 3.6px;
        margin: 0 0 18px 0;
        line-height: 24px;
    }

    /* .block__text */

    &__text {
        line-height: 34px;
        font-size: 18px;
        font-weight: 400;
        color: #848789;
        &_j {
            text-align: justify;
            font-size: 16px;
            line-height: 30px;
            &::after {
                content: '';
                width: 50px;
                height: 1px;
                background-color: #ebebeb;
                display: block;
                margin: 35px auto 0 auto;
            }
        }
    }
    @media screen and (max-width: 1500px) {
        &__text {
            &_j {
                &::after {
                    margin: 15px auto 0 auto;
                }
            }
        }
    }
}

.brands {
    display: block;
    &_reverse {
        .brands__col {
            order: 1;
            &_b {
                order: 2;
            }
        }
    }

    /* .brands__row */

    &__row {
        display: flex;
    }

    /* .brands__col */

    &__col {
        flex: 0 1 33.333%;
    }

    /* .brands__col_b */

    &__col_b {
        flex: 0 1 66.667%;
    }

    /* .brands__content */

    &__content {
        padding: 70px 65px 55px 65px;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            bottom: -10px;
            left: 50%;
            margin-left: -10px;
            width: 0;
            height: 0;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-top: 10px solid #fff;
            z-index: 1;
        }
    }

    /* .brands__image */

    &__image {
        padding: 0 0 37.5% 0;
    }
    
    @media screen and (max-width: 1500px) {
        &__content {
            padding: 40px 35px 35px 35px;
        }
    }
    @media screen and (max-width: 1100px) {
        &__row {
            flex-direction: column;
        }
        &_last {
            .brands__col {
                &:nth-child(1) {
                    order: 2;
                }
                &:nth-child(2) {
                    order: 1;
                }
                &:nth-child(3) {
                    order: 3;
                }
            }
        }
    }
}

.item {
    position: relative;
    display: block;
    height: 100%;
    /* .item__logo */

    &__logo {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 2;
        opacity: 0;
        visibility: hidden;
        transition: all .8s ease;
    }
    &:hover {
        .item__logo {
            opacity: 1;
            visibility: visible;
            }
        }

    /* .item__bg */

    &__bg {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
    @media screen and (max-width: 1100px) {
        height: auto;
        padding: 0 0 70% 0;
    }
}
