.header {
    position: absolute;
    width: 100%;
    z-index: 50;
    padding-top: 65px;
    /* .header__row */

    &__row {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
.header-nav {
    display: flex;
    align-items: center;


    /* .header-nav__item */

    &__item {
        padding: 0 37.5px;
        position: relative;
        &:before {
            content: '';
            position: absolute;
            width: 3px;
            height: 3px;
            background-color: #4d4959;
            border-radius: 50%;
            right: 0;
            top: 50%;
        }
        &:nth-child(2),
        &:last-child {
                &:before {
                    display: none;
                }
            }
        &:nth-child(2) {
            padding: 0 120px 0 37.5px;
        }
        &:nth-child(3) {
            padding: 0 37.5px 0 120px;
        }

        a {
            color: #4d4959;
            font-size: 13px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 2.6px;
            text-decoration: none;
        }
    }

    /* .header-nav__logo */

    &__logo {
        position: absolute;
        top: 30px;
    }
}
