@media screen and (max-width: 768px) {
    .header-nav {
        flex-direction: column;
        position: fixed;
        width: 100%;
        height: 100%;
        background-color: #4d4959;
        top: 0;
        left: 0;
        padding: 150px 0 0 0;
        &__logo {
            position: absolute;
            top: 15px;
            z-index: 20;
        }
        &__item {
            padding: 30px 0 !important;
            a {
                color: #ffffff;
                font-size: 20px;
            }
            &:before {
            content: '';
            width: 4px;
            height: 4px;
            background-color: #fff;
            left: 50%;
            top: 80px;
            }
            &:nth-child(2) {
                &:before {
                    display: block;
                }
            }
        }
    }
    #menu__toggle {
        opacity: 0; }
    .menu__btn {
        display: flex;
        align-items: center;
        position: fixed;
        top: 40px;
        right: 30px;
        width: 40px;
        height: 40px;
        cursor: pointer;
        z-index: 10; }
    .menu__btn > span,
    .menu__btn > span::before,
    .menu__btn > span::after {
        display: block;
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: #000000; }
    .menu__btn > span::before {
        content: '';
        top: -12px; }
    .menu__btn > span::after {
        content: '';
        top: 12px; }
    .header-nav {
        z-index: 5;
        visibility: hidden;
        top: -120%;
        width: 100%;
        text-align: center;
        
        box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2); }
    #menu__toggle:checked ~ .menu__btn > span {
        transform: rotate(45deg); }
    #menu__toggle:checked ~ .menu__btn > span::before {
        top: 0;
        transform: rotate(0);
        background-color: #ffffff; }
    #menu__toggle:checked ~ .menu__btn > span::after {
        top: 0;
        transform: rotate(90deg);
        background-color: #ffffff; }
    #menu__toggle:checked ~ .header-nav {
        visibility: visible;
        top: 0; }
    .menu__btn > span,
    .menu__btn > span::before,
    .menu__btn > span::after {
        transition-timing-function: ease-in-out;
        transition-duration: .5s; }
    .header-nav {
        transition-duration: .5s;
        transition-timing-function: ease-in-out; }
    .header-nav__item {
        transition-duration: .5s;
        transition-timing-function: ease-in-out; }
}